.modal {
    background-color: rgba(0, 0, 0, 0.4);
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    display: none;
    justify-content: center;
}
.modal-panel{
    background: white;
    padding: 10px;
    transform: translate(0, 100px);
    margin-bottom: auto;
    box-shadow: 5px 10px #888;
    border-radius: 5px;
    font-family: Roboto,sans-serif;
    color: rgba(0,0,0,.75);
    margin-top: 100px;
}
.modal-panel-base{
    background: white;
    padding: 10px;
    margin-bottom: auto;
    box-shadow: 5px 10px #888;
    border-radius: 5px;
    font-family: Roboto,sans-serif;
    color: rgba(0,0,0,.75);
}
.modal-abierto{
    display: grid;
}
.modal-background{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 20;
}
.modal-container{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: auto;
    padding: 1em;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    text-align: center;
}
.loader {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}