input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
  display: none;
}
/* Works for Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
input[type="number"]:hover,
  input[type="number"]:focus {
    -moz-appearance: number-input;
  }