.header {
  z-index: 10;
  position: fixed;
  top: 0;
  left:0;
  width: 100%;
  height: 64px;
  background-color: white;
  color: white;
  display: flex;
  align-items: center;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  justify-content: space-between;
}
.header-logo{
  top:10px;
  margin: 10px;
  justify-content: left;
}
.header-buttons{
  justify-content: right;
}