.footer{
    z-index: 10;
    position: fixed;
    left: 0px;
    right: 0px;
    bottom: 0px;
    width: 100%;
    height: 64px;
    padding: 0;
    background-color: rgb(122, 149, 171);
    color: white;
    display: flex;
    align-items: center;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    justify-content: center;
    font-size: .875rem;
    font-family: Roboto,sans-serif;
}